.home-page-container1{
    background-color: #191919;
    max-width:100vw;
    padding:5% 20% 5% 20%;
    text-align: center;
}
.home-page-container1-heading{
    color:#ffc03a;
    font-size: 72px;
    line-height: 1.25em;
}

@media (max-width:768px){
    .home-page-container1-heading{
        font-size: 30px;
    }.home-page-container1-text{
        font-size: 12px;
    }
}
.home-page-container1-text{
    color: #ffffff;
    font-size: 15px;
    line-height: 1.875em;
}
.home-page-container2{
    max-width: 100vw;
    padding: 40px 5% 5%;
    background-color: #000000;
    text-align: center;
}
.home-page-container2-heading{
    color:#ffc03a;
    font-size: 40px;
    margin-top:25px ;
}
.home-page-container2-text{
    color: #ffffff;
    font-size: 15px;
    line-height: 1.875em;
}
@media (max-width:768px){
    .home-page-container2-heading{
        font-size: 20px;
    }.home-page-container2-text{
        font-size: 12px;
    }
}
.home-page-container2-subcontainer{
    background-color: #191919;
    display: flex;
    justify-content: space-between;
    margin:5% 0 5% 0;
}

.home-page-container2-subcontainer-sub1{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
    max-width:50%;
}
.home-page-container2-subcontainer-sub-heading{
    font-size: 22px;
    color: #ffc03a;
}

.home-page-container2-subcontainer-sub-text{
    color: #ffffff;
    margin: 0px 10% 0 10%;
}
.home-page-container2-subcontainer-sub2{
    max-width:50%;
}
@media (max-width:768px){
    .home-page-container2-subcontainer{
        flex-direction: column;
    }.home-page-container2-subcontainer-sub1{
        max-width:100%;
    }.home-page-container2-subcontainer-sub2{
        margin-top: 50px;
        max-width:100%;
    }
}
.home-page-container2-subcontainer-image{
    max-width: 100%;
}
.home-page-container3{
    background-color: #191919;
    padding: 5% 15%  5% 15%;
    text-align: center;
}
.home-page-container3-image{
    max-width: 100%;
    
}
.home-page-container4{
    background-color: #000000;
    padding: 5% 15%  5% 15%;
    text-align: center;
}
.home-page-container4-button{
    background-color: #ffc03a;
    border: none;
    padding: 16px;
    font-size: 15px;
    color: #ffffff;
    margin-top: 20px;
}
.home-page-form1-input-container{
    display: flex;
    flex-direction: column;
    width:48%;
    margin-bottom: 20px;
}
.home-page-form1-label{
    color:#ffffff;
    align-self: flex-start;
    margin-bottom: 10px;
}
.home-page-form1-input{
    background-color: transparent;
    border-color: #ffffff;
    height:35px;
    color:#fff;
}
.home-page-form1-container1{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.home-page-form1-textarea-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    
}
.home-page-form1-textarea{
    width:100%;
    height:100px;
    background-color: transparent;
    border-color: #ffffff;
    color:#fff;
}
.home-page-form1-button{
    margin-top:25px;
    background-color: #ffffff;
    border-color: #ffffff;
    color: #000000;
    padding:15px 50px 15px 50px;
    font-size: 18px;
    font-size: 600;
}
.home-page-form2{
    display: flex;
    flex-direction: column;
}
.home-page-form2-heading{
    color: #ffffff;
    margin-bottom: 0px;
    align-self: flex-start;
    font-size: 24px;
}
.home-page-form2-text{
    color: #ffffff;
    margin-top: 0px;
    align-self: self-start;
}
.home-page-form2-input{
    width:99.5%;
    height:35px;
    color: #fff;
}
.home-page-form2-button{
    width: 100%;
    margin-top: 20px;
    height:38px;
}














.home-page-container5{
    background-color: #191919;
    padding: 5%;
    display: flex;
    max-width: 100%;
}

.home-page-container5-sub1{
    background-color: #000000;
    width: 50%;
}
.home-page-container5-sub2{
    width: 50%;
}
.home-page-container5-image{
    width: 100%;
}
@media (max-width:768px){
    .home-page-container5{
        flex-direction: column;
    }.home-page-container5-sub1{
        width: 100%;
    }
    .home-page-container5-sub2{
        width: 100%;
    }
}

.home-page-form3-input-container{
    display: flex;
    flex-direction: column;
    width:100%;
    margin-bottom: 20px;
}
